.scrolling-wrapper-flexbox {
  padding: 20px;

  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  &::-webkit-scrollbar {
    display: none;
  }
}
.card {
  flex: 0 0 auto;
  margin: 20px;
  max-width: 300px;
  height: auto;
}
